import { FC } from 'react';

import { Flex, Tabs, registerIcon } from '@pega/cosmos-react-core';
import * as arrowMicroLeftIcon from '@pega/cosmos-react-core/lib/components/Icon/icons/arrow-micro-left.icon';

import CaseHeader from './CaseHeader';
import { StyledCaseSummary, StyledCaseSummaryInfo } from './CaseView.styles';
import { useCaseViewContext } from './CaseView.context';

registerIcon(arrowMicroLeftIcon);

const CaseSummary: FC = () => {
  const { summary, summaryExpanded, tabs, aboveMD, isPreview } = useCaseViewContext();

  return (
    <Flex as={StyledCaseSummary} container={{ direction: 'column' }} item={{ shrink: 0 }}>
      <CaseHeader />

      {aboveMD && !isPreview && summaryExpanded && (summary || tabs.items.length > 1) && (
        <StyledCaseSummaryInfo>
          {summary}

          {tabs.items.length > 1 && (
            <Tabs
              type='vertical'
              tabs={tabs.items}
              onTabClick={tabs.onClick}
              currentTabId={tabs.currentTabId}
            />
          )}
        </StyledCaseSummaryInfo>
      )}
    </Flex>
  );
};

export default CaseSummary;
